import React from 'react';
import {Link} from "react-router-dom";
import Moment from 'react-moment';

const QuestItem = ({quest}) => {
  console.log(quest.is_assigned);
  const bg_image = {
    backgroundImage: `url(${quest.logo})`
  };
  const asignee = () => {
    if (quest.is_assigned) {
      console.log(1);
      return (
        <div className="is-assigned">You assigned to quest</div>
      )
    } else {
      console.log(2);
      return (
        <div className="not-assigned">You not assigned to quest</div>
      )
    }
  };

  return (
    <div className="quest-item" style={bg_image}>
      <div className="quest-item-wrapp">
        <p className="card-title">{quest.name}</p>
        <p className="card-date">
          <Moment fromNow>{quest.start_date}</Moment>

        </p>
        <Link className="btn" to={`/quest/${quest.id}`}>Detail</Link>
        <div className="assignee">
          {asignee}
        </div>
      </div>
    </div>
  )
}
export default QuestItem;


